      background-image: url("data:image/svg+xml;base64,");
  }
  .lb-icon-arrow{
      background-image: url("data:image/svg+xml;base64,");
  }
  .lb-icon-zoomin{
      background-image: url("data:image/svg+xml;base64,");
  }
  .lb-icon-zoomout{
      background-image: url("data:image/svg+xml;base64,");
  }
  .lb-icon-rotate{
      background-image: url("data:image/svg+xml;base64,");
  }
  .lb-icon-reset{
      background-image: url("data:image/svg+xml;base64,");
  }
  .lb-container{
      z-index: 50000;
      position: absolute;
      position: fixed;
      left:0px;
      top:0px;
      width: 100%;
      height: 100%;
      background-color: #000;
      background-color: rgba(0,0,0,0.8);
      display: flex;
      flex-direction: column;
  }
  .lb-container .lb-header{
      height: 50px;
      background: #000;
      background: rgba(0,0,0,0.9);
      box-shadow: 0px 1px 5px 0px #111;
      box-sizing: border-box;
      display: flex;
      align-items: stretch;
      justify-content: flex-end;
      z-index: 10;
  }
  .lb-header .lb-button{
      padding: 10px 25px;
      background-size: 40% auto;
      background-position: center center;
      background-repeat: no-repeat;
      cursor: pointer;
  }
  .lb-header .lb-button:not(:last-child){
      margin-right: 3px;
  }
  .lb-header .lb-button:hover{
      background-color:#777;
  }
  
  .lb-header .lb-button.rotater{
      transform: rotateY(180deg);
  }
  .lb-header .lb-button.next{
      transform: rotateY(180deg);
  }
  .lb-header .lb-title{
      flex: 1;
      align-items: center;
      color: white;
      font-size: 1.1em;
      padding: 0em 0.8em;
      overflow: hidden;
  }
  .lb-header .lb-title span{
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      width: 100%;
  }
  .lb-container .lb-canvas{
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      max-width: 100%;
      max-height: 100%;
      position: relative;
  }
  .lb-canvas .lb-img{
      max-width: 80vw;
      max-height: 80vh;
      min-width: 100px;
      min-height: 100px;
      /* transition: all 0.1s; */
      cursor: grab;
      background-size: 50px;
      transform-origin: center center;
  }
  .lb-canvas .lb-img.lb-loading{
      opacity: 0;
  }
  .lb-canvas.lb-loading{
      background-image: url("data:image/svg+xml;base64,");
      background-position: center center;
      background-repeat: no-repeat;
  }
  .mobile-controls{
      position: absolute;
      bottom: 25px;
      left: 0px;
      width: 100%;
      height: 45px;
      display: flex;
      justify-content: center;
  }
  .mobile-controls .lb-button{
      width: 50px;
      height: 50px;
      background-color: rgba(0,0,0,0.9);
      background-position: center center;
      background-size: 40%;
      background-repeat: no-repeat;
      margin: 0.2em;
      cursor: pointer;
  }
  .mobile-controls .lb-button:active{
      background-color:#777;
  }
  .mobile-controls .lb-button.next{
      transform: rotateY(180deg);
  }
  .lb-show-mobile{
      display: none;   
  }
  .lb-disabled{
      pointer-events: none;
      touch-action: none;
      opacity: 0.4;
  }
  @media (max-width:600px){
      .lb-header .lb-button:hover{
          background-color: inherit;
      }
      .lb-header .lb-button:active{
          background-color: #777;
      }
      .lb-show-mobile{
          display: flex !important;
      }
      .lb-hide-mobile{
          display: none !important;
      }
      .lb-canvas .lb-img{
          max-height: 60vh;
      }
  }